html,
body {
  margin: 0;
  padding: 0;
}

.App {
  padding: 20px;
}

nav.navbar {
  display: flex;
  padding: 0px 20px;
  background: white;
  border-bottom: black solid 5px;
}

button.navbar-button {
  padding: 5px 20px;
  width: 120px;
  margin: 15px 10px;
  border-radius: 3px;
  font-family: "Courier New", Courier, monospace;
}
.rightnav {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: flex-end;
}

#home-btn {
  color: black;
  text-decoration: none;
}

.indeepTitle {
  font-family: "Syne Mono", "Courier New", Courier, monospace;
  font-weight: 700;
  font-size: 80px;
}

.indeepText {
  font-family: "Syne Mono", "Courier New", Courier, monospace;
}

.mb2 {
  margin-left: 10px;
  margin-right: 10px;
  font-family: "Teko", sans-serif;
  font-size: 22px;
}

.formContainer {
  padding: 20px;
}

.sort-list {
  font-family: "Syne Mono", "Courier New", Courier, monospace;
  font-size: 28px;
}

.forms-input {
  padding: 80px 0 0 0;
  font-family: "Syne Mono", "Courier New", Courier, monospace;
}

.rotate {
  animation: rotation 5s infinite linear;
  margin: 15px;
 
  width: 100px;
  height: 100px;
}
.record,
.card {
  box-shadow: 0 0 10px 0 rgba(100, 100, 100, 0.26);
  margin: 20px 0;

  padding: 5px;
  /*margin: 25px;*/
}

p {
  margin: 0;
  padding: 0;
  line-height: 15px;
}

.chart-text-title {
  font-family: "Teko", sans-serif;
  font-size: 30px;
  align-items: center;
}

.chart-text {
  font-family: "Teko", sans-serif;
  font-size: 24px;
}

.card {
  margin: 0px;
}
.fave-card {
  display: inline;
  width: 25%;
  line-height: 20px;
}
.details-labels {
  font-weight: 400;
  font-family: "Courier New", Courier, monospace;
}
h1,
p {
  font-family: "Courier New", Courier, monospace;
  font-size: 14px;
}
.chart-list {
  border-width: 5px;
  border-bottom: black;
  margin-bottom: 5px;
  margin-top: 15px;
  padding: 10px;
  /*  height: 200px;
  width: 200px; */

  font-size: 11px;
  font-weight: 600;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.charts-div {
  background-color: rgb(89, 196, 196);
  padding: 10px;
  margin: 0 0 0 20px;
}

.cardList {
  list-style: none;
  list-style-position: unset;
}

/* unvisited link */
a:link {
  color: rgb(2, 83, 2);
}

/* visited link */
a:visited {
  color: green;
}

/* mouse over link */
a:hover {
  color: red;
}

/* selected link */
a:active {
  color: yellow;
}

.record-list-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-bottom: 50px;
}
.record-list {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 5px;
}

.footer {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  background-color: rgba(92, 123, 123);
  /* margin-top: 40rem; */
  padding: 1rem;

  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
.details-labels .col-lg-6 {
  padding-left: 30px;
}
body {
  margin-bottom: 50px;
}
.footer > img {
  height: 30px;
}
